.navbar {
  background-color: purple !important; 
  position: fixed;
  width: 100%;
  z-index: 30;
}

.navbar-nav {
  width: 100%;
}

.navbar li {
  display: inline-block;
  padding: 10px 0;
  width: 33.3%;
  text-align: center;
  color: #ffffff;
  font-size: 1.5em;
}

.navbar-brand {
  color: #ffffff !important;
  font-weight: bold;
  font-size: 1.1em;
}

.nav-other {
  /* padding-top: 44px !important; */
